@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans&family=Source+Code+Pro&display=swap");
body {
  margin: 0;
  font-family: "Source Code Pro", monospace;
}

nav {
  background-color: #212227;
  padding: 16px;
  font-size: 16px;
  box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.5019607843);
  z-index: 1;
}
nav a {
  color: #a0a0a0;
  margin-right: 24px;
  font-family: "Josefin Sans", sans-serif;
}

.app {
  background-color: #13141a;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  height: 100vh;
}

.content {
  overflow-y: auto;
  flex-grow: 1;
}

.landing {
  min-height: auto;
  display: flex;
  flex-grow: 1;
}
.landing * {
  color: #a0a0a0;
}
.landing > * {
  display: flex;
  flex-direction: column;
}
.landing .left {
  width: 33vw;
  margin: 80px 0 80px 80px;
}
.landing .right {
  width: 67vw;
  align-items: center;
  justify-content: center;
}
.landing .hi {
  color: #a0a0a0;
  font-size: 40px;
  margin-bottom: 16px;
}
.landing img.extended-logo {
  width: 300px;
  margin-bottom: 72px;
}
.landing img.headshot {
  width: 30vw;
}
.landing .mission-statements div {
  margin-bottom: 32px;
}
.landing .mission-statements b.position {
  color: #e0e0e0;
}

.skills {
  display: flex;
  flex-direction: row;
  padding: 0 40px;
  margin-top: 18px;
  margin-bottom: 64px;
}
.skills .card-wrapper {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  width: 100%;
}
.skills .card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #212227;
  max-width: 400px;
  width: 25vw;
  height: 100%;
  color: #a0a0a0;
}
.skills .card .card-top {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.skills .card-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: stretch;
}
.skills .title-wrapper {
  width: auto;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  font-family: "Josefin Sans", sans-serif;
  color: #1e65a7;
  background-repeat: no-repeat;
  background-size: cover;
  font-weight: bold;
  font-size: 32px;
  padding: 25% 10%;
}
.skills .title-wrapper .material-icons-sharp {
  font-size: 36px;
  margin-right: 8px;
  align-self: center;
}
.skills .section {
  flex-grow: 1;
  padding: 24px 24px 0;
}
.skills .section .section-title {
  color: #e0e0e0;
  text-align: center;
  font-size: 18px;
}
.skills .section li {
  padding: 8px;
}
.skills .button-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}
.skills .expanding-button {
  width: 50%;
  margin: 32px 0;
}

.project-page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 20vw;
}
.project-page .header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.project-page .header .title {
  font-family: "Josefin Sans", sans-serif;
  color: #1e65a7;
  font-size: 96px;
}
.project-page .header .flavor {
  margin-bottom: 16px;
  color: #e0e0e0;
}
.project-page .header .description {
  color: #a0a0a0;
  font-size: 16px;
}
.project-page .projects {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
}
.project-page .projects .project {
  width: 100%;
  background-color: #212227;
  display: flex;
  flex-direction: column;
  padding: 32px;
  margin-bottom: 32px;
}
.project-page .projects .project .header {
  display: flex;
  flex-direction: row;
  align-items: baseline;
  justify-content: space-between;
  height: auto;
}
.project-page .projects .project .flavor {
  color: #e0e0e0;
  margin-bottom: 32px;
}
.project-page .projects .project .title {
  color: #1e65a7;
  font-family: "Josefin Sans", sans-serif;
  font-size: 48px;
}
.project-page .projects .project .date {
  color: #a0a0a0;
}
.project-page .projects .project .description {
  color: #a0a0a0;
}
.project-page .numbers {
  display: grid;
  color: #e0e0e0;
}
.project-page .numbers .number-row {
  display: grid;
  grid-auto-columns: minmax(0, 1fr);
  grid-auto-flow: column;
}
.project-page .numbers .number-row .big-number-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 32px;
}
.project-page .numbers .number-row .big-number-container .big-number {
  font-family: "Josefin Sans", sans-serif;
  color: #1e65a7;
  font-size: 64px;
}

ul {
  padding: 0 0 0 8px;
  margin: 0;
}

li {
  padding-left: 8px;
  text-align: center;
}
li::marker {
  content: "";
  font-size: 18px;
}

a {
  color: inherit;
  transition: 0.1s ease-in-out;
  text-decoration: none;
}
a:hover {
  color: #25b396;
}

.icon-button {
  padding: 8px;
  display: flex;
  transition: all 0.1s ease-in-out;
  cursor: pointer;
  fill: #e0e0e0;
}
.icon-button:hover {
  background-color: rgba(37, 179, 150, 0.1254901961);
  color: #25b396;
  fill: #25b396;
}
.icon-button:active {
  background-color: rgba(30, 101, 167, 0.1254901961);
  color: #1e65a7;
  fill: #1e65a7;
}

.expanding-button {
  background-color: rgba(37, 179, 150, 0.1254901961);
  padding: 8px;
  color: #25b396;
  font-family: "Josefin Sans", sans-serif;
  display: flex;
  justify-content: center;
  font-size: 18px;
  cursor: pointer;
  border: 1px solid transparent;
  transition: 0.1s ease-in-out;
}
.expanding-button:hover {
  border-color: #25b396;
}

footer {
  background-color: #212227;
  color: #e0e0e0;
  padding: 16px;
}
footer .icons {
  display: flex;
}
footer .top {
  display: flex;
  flex-direction: row;
}
footer .top h1 {
  margin: 0;
}
footer .top > div {
  flex-grow: 1;
}

